.box {
  margin-right: 20px;
}

.ul {
  list-style: none;
}

.ul > li {
  display: flex;
  margin-bottom: 50px;
  align-items: center;
}

.ul > li > div {
  text-align: start;
}

.ul > li > div > h3 {
  margin: 0px;
}

.icon {
  margin-top: 8px;
  margin-right: 5px;
  color: slateblue;
}
.icon-dark {
  margin-top: 8px;
  margin-right: 5px;
  color: white;
}

.subtitle {
  font-size: small;
}

.title-dark {
  color: darkorange;
}
.subtitle-dark {
  font-size: small;
  color: rgb(180, 180, 180);
}

@media only screen and (max-width: 600px) {
    .ul{
        margin: 0px 20px;
    }
    .ul > li > div{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }
    .ul > li {
      margin-bottom: 20px;
    }
    .icon {
      margin-top: 3px;
      margin-right: 5px;
      color: slateblue;
    }
    .icon-dark {
      margin-top: 3px;
      margin-right: 5px;
      color: white;
    }
  }